<template>
  <div class="hello">
    <header>
      <h4 style="margin: 5px">儿童视频管理后台{{ bIndex }}</h4>
      <div>
        <span class="bt" @click="sendEvent('input keyevent 24')">音量+</span>
        <span class="bt" @click="sendEvent('input keyevent 25')">音量-</span>
        <span class="bt" @click="sendEvent('input keyevent 23')">暂停</span>
        <span class="bt" @click="sendEvent('input keyevent 4')">返回</span>
        <div style="float: left">
          MODE:
          <span class="bt">顺序</span>
          <span class="bt">随机</span>
          <span class="bt">单个</span>
        </div>
      </div>
    </header>
    <div>
      <ul>
        <li class="it" v-for="(it, k) in items" :key="it.aid">
          <img
            class="coverUrl"
            :src="it.coverUrl"
            referrerPolicy="no-referrer"
          />
          <div>
            <div>
              <b>#{{ k }}</b
              >{{ it.title || it.aid }}({{ it.aid }})
            </div>
            <div>
              <li v-for="(item, i) in it.items" :key="item.path">
                <span
                  :class="{ cur: aIndex == k && bIndex == i }"
                  @click="play(k, i)"
                  class="url"
                  :title="item.title"
                  >{{ it.title && it.title != "dy" ? i + 1 : item.title }}</span
                >
                <em class="url" @click="openUrl(k, i)">*</em>
              </li>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <player @updateProgress="updateProgress" :curAid="items[aIndex]"></player>
  </div>
</template>

<script>
import Player from "./Player";
export default {
  components: { Player },
  name: "Main",
  data: () => {
    return {
      items: [],
      aIndex: 0,
      bIndex: 0,
      progress: 0,
      duration: 0,
    };
  },
  mounted: function () {
    this.getDatas();

    setInterval(() => {
      this.getStatus();
    }, 2000);
  },
  props: {
    msg: String,
  },
  methods: {
    updateProgress(percent, isPlay) {
      this.play(this.aIndex, this.bIndex, percent, isPlay);
    },
    openUrl(aIndex, bindex) {
      open("/api/url?aIndex=" + aIndex + "&bIndex=" + bindex);
    },
    base64(item) {
      let n = "data:image/png;base64," + item.thumb;
      console.log(n);
      return n;
    },
    sendEvent(str) {
      fetch("/api/event", {
        method: "post",
        body: JSON.stringify({ event: str }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },
    play(k, i, progressPercent = 0, isPlay = true) {
      this.aIndex = k;
      this.bIndex = i;
      fetch("/api/play", {
        method: "post",
        body: JSON.stringify({
          aIndex: k,
          bIndex: i,
          progress: progressPercent,
          isPlaying: isPlay,
        }),
        headers: {
          "Content-type": "application/x-www-form-urlencoded",
        },
      })
        .then((data) => {
          return data.text();
        })
        .then((val) => {
          console.log(val); //  输出请求到的字符串
        });
    },

    getDatas() {
      fetch("/api/list.json", {
        method: "GET",
        mode: "cors",
        credentials: "include",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },

    getStatus() {
      fetch("/api/status", {
        method: "GET",
      })
        .then((response) => {
          response.json().then((data) => {
            Object.assign(this, data);
            this.$store.commit("updateStatus", data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
  },
};
</script>

